<template>
  <div>
    <settledinStores v-if="pageStatus === 2" />
    <certification v-if="pageStatus === 1" />
    <nosettled :info="info" v-if="pageStatus === -1" />
    <underReview v-if="pageStatus === 0"/>
  </div>
</template>
<script>
import nosettled from "../../components/settleinStatus/notCertification.vue";
import certification from "../../components/settleinStatus/certification.vue";
import settledinStores from "../../components/settleinStatus/settledinStores.vue";
import underReview from '../../components/settleinStatus/underReview.vue'
export default {
  components: {
    nosettled,
    certification,
    settledinStores,
    underReview
  },
  data: () => ({
    pageStatus: -2,
    info:""
  }),
  created() {
    console.log(JSON.parse(this.$store.state.userInfo).access_token);
    this.$axios.get("ym/mall-online-merchant/status").then(res => {
      this.pageStatus = res.data.status;
      this.info = res.data.auditOpinion
      console.log(res);
    });
  },
  mounted() {}
};
</script>